import { FormManager } from '../Common/FormManager';
import { onDocReady } from '../Common/UiHelper';

export class InquiryStatusManager {

    async Init(): Promise<void> {
        let submitButton = document.getElementById("submitButton") as HTMLButtonElement;
        if (submitButton !== null) {
            submitButton.addEventListener("click", (e: Event) => this.Save());
        }
    }

    async Save(): Promise<void> {
        let formManager = new FormManager('InquiryStatusForm');
        if (await formManager.ValidateForm()) {
            let url = `${window.location.href}`;
            let response = await formManager.SubmitForm(url);
            if (response.ok) {    

                let successMessage = `<div class="alert alert-success"><b>ORDER COMPLETE!</b></div><hr class="my-4"> <h1 class="h3">Thank You!</h1><p>${response.data}</p>`
                let headerMessagElement = document.getElementById('HeaderAlertMessage') as HTMLDivElement;
                if (headerMessagElement !== null) {
                    headerMessagElement.innerHTML = successMessage;
                    headerMessagElement.style.display = 'block';
                }
                let formElement = document.getElementById('InquirySubmitDiv') as HTMLDivElement;
                if (formElement !== null) {
                    formElement.style.display = 'none';
                }
            }else{
                await formManager.ShowSuccessFailureMessage(response);
            }
        }
    }
}
onDocReady(async () => {
    let page = new InquiryStatusManager();
    await page.Init();
});